import React from "react";
import { Link } from "react-router-dom";
import { routerPath } from "webPath";

function SignUpMain() {
  return (
    <>
      <div>
        <h1></h1>
      </div>
      <div>
        <button>
          <Link to={routerPath.myPage_url}>  </Link>
        </button>
        <button>
          <Link to="/mypage"> 2</Link>
        </button>
        <button>
          <Link to={routerPath.main_url}>  </Link>
        </button>
      </div>
    </>
  );
}

export default SignUpMain;
